<template>
    <div class="wrapper">
        <h1>C–ø–∏—Å–æ–∫ –∫–æ–º–Ω–∞—Ç</h1>
        <div class="createRoomWrapper">
            <h2>–°–æ–∑–¥–∞—Ç—å –∫–æ–º–Ω–∞—Ç—É</h2>
            <label>–ü–∞—Ä–æ–ª—å: <input v-model="password" type="text" placeholder="–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ"></label>
            <button v-on:click="createRoom()">–°–æ–∑–¥–∞—Ç—å</button>
        </div>
        <div v-for="(room, id) in rooms" :key="id" class="room" :class="{'red': room.started || room.capacity === 6}">
            <span>–ö–æ–º–Ω–∞—Ç–∞ –∏–≥—Ä–æ–∫–∞: {{ room.user }} </span> |
            <span>–°–≤–æ–±–æ–¥–Ω—ã—Ö –º–µ—Å—Ç: {{ 6 - room.capacity }} </span> |
            <span>–ü–∞—Ä–æ–ª—å: {{ room.password ? '–µ—Å—Ç—å': '–Ω–µ—Ç' }} </span> |
            <button @click="join(room)">–í–æ–π—Ç–∏</button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            rooms: {},
            room: null,
            password: ''
        }
    },
    props: {
        modelValue: { type: Object },
        socket: { type: WebSocket }
    },
    methods: {
        createRoom() {
            this.socket.send(JSON.stringify({event: 'createRoom', password: this.password}));
            return;
        },
        join(room) {
            const message = {event: 'join', roomId: room.id};
            if (room.password) {
                let password = prompt('–í–≤–µ–¥–∏—Ç–µ –ø–∞—Ä–æ–ª—å');
                Object.assign(message, {password});
            }
            this.socket.send(JSON.stringify(message));
        },
        handler(event) {
            const message = JSON.parse(event.data);

            if (message.event === 'createRoom') {
                this.rooms[message.data.id] = message.data;
            }
            else if (message.event === 'joinRoom') {
                this.$emit('update:modelValue', message.data); 
            }
            else if (message.event === 'error') {
                alert(message.data);
            }

            //this.$emit('update:modelValue', this.userName); 
        }
    },
    async mounted() {
        this.socket.addEventListener('message', this.handler);
        this.socket.send(JSON.stringify({event: 'roomList'}));
    },
    unmounted() {
        this.rooms = null;
        this.socket.removeEventListener('message', this.handler);
    }
}
</script>

